import { useEffect, useState } from 'react';
import './Register.scss';
import '../MainTemplate/MainTemplate';
import logo from "../../logoAndPhotos/ibring.jpg";
import { useNavigate } from 'react-router-dom';
import { useAppDispatch, useAppSelector } from '../../../app/hooks';
import { logintAsync } from '../../../features/userLogin/userLoginReducer';
import axios from 'axios';
import { Link } from "react-router-dom";

function Register() {
    const userLogin = useAppSelector(state => state.logged);
    const dispatch = useAppDispatch();
    const nav = useNavigate();

    useEffect(() => {
        console.log(userLogin);
        if (userLogin.status === 'logged') {
            nav('/greetings');
            console.log("logged");
        }
    }, [userLogin]);

    function signUp() {
        return new Promise(async (resolve, reject) => {
            await axios.post('/user/signUp', {
                email: email.toLowerCase(), pass: pass
            }).then(data => {
                console.log(data.data)
                if (data.data.ok) {
                    resolve(true);
                } else {
                    resolve(false);
                }
            })
                .catch(err => reject(err))
        })
    }



    const [email, setEmail] = useState("");
    const [pass, setPass] = useState("");

    function handleEmailPassSignUp(e: any) {
        e.preventDefault();
        console.log({ email })
        console.log({ pass })

        signUp()
            .then((json) => {
                console.log("isUer:", json);
                if (json) {
                    dispatch(logintAsync({ email: email, pass: pass }));
                    // nav('/greetings');
                } else {
                    console.log("user doesn't exists!");
                }
            })
            .catch((err) => {
                console.error(err);
            });
    }

    return (
        <div className="mainTemplate">
            <div className="mainHeader">
                <img className='registerLogo' src={logo} alt="" />
            </div>
            <div className="mainContent">
                <label className="templateTitle signUpTitle">let's sign up</label>
                <form onSubmit={(ev: any) => ev.preventDefault()} className='signInButtonGoogle' >
                    <button className='templateButton gmailSignUp' type="submit">
                        <img src="data:image/png;base64," alt="" />
                        <label>Gmail</label>
                    </button>
                </form>

                <form onSubmit={(ev: any) => ev.preventDefault()} className='signInButtonFaceBook' >
                    <button className='templateButton gmailSignUp' type="submit">
                        <img src="https://upload.wikimedia.org/wikipedia/commons/thumb/0/05/Facebook_Logo_%282019%29.png/1200px-Facebook_Logo_%282019%29.png" alt="" />
                        <label>Facebook</label>
                    </button>
                </form>

                <div className="orSplit">
                    <div className="orWord">or</div>
                </div>

                <form onSubmit={handleEmailPassSignUp} className='loginWithEmailAndPass' >
                    <input className='templateInput EmailRegistered' placeholder="Email" type="email" required onKeyUp={(ev: any) => { setEmail(ev.target.value) }} />
                    <input className='templateInput passRegistered' placeholder="Password" type="password" required onKeyUp={(ev: any) => { setPass(ev.target.value) }} />
                    <label className='haveAccount'>Already have an Account? <span><Link to="/login">Sign in here</Link></span></label>
                    <input className='templateButton signUpButton' type="submit" value="sign up" />
                </form>

                {userLogin.status === 'loading' ?
                    <div>Loading</div>
                    : <></>
                }
            </div>
        </div>
    );
}

export default Register;